.header {
  background-color: var(--dark-color);
  width: 100%;
  position: fixed;
}

.logo-container {
  cursor: pointer;
  outline: none;
  align-items: center;
  display: flex;
}

.logo-image {
  border-radius: 1000px;
  width: 146px;
  height: 82px;
}

.logo-text {
  color: var(--light-color);
  margin-left: 12px;
  font-size: 36px;
}

.logo-text:hover {
  color: var(--active-color);
}

.nav-link {
  color: var(--light-color);
  font-size: 30px;
}

.nav-link.active, .nav-link:hover, .nav-link:focus, .nav-link.current {
  color: var(--active-color);
}

.add-task-button {
  background-color: var(--light-color);
  color: var(--dark-color);
  border-radius: 1000px;
  justify-content: center;
  align-items: center;
  width: 178px;
  height: 78px;
  font-size: 20px;
  font-weight: 500;
  display: flex;
}

.add-task-button:hover {
  background-color: var(--active-color);
}

.navbar-brand {
  padding-top: 0;
  padding-bottom: 0;
}

.navbar-toggler {
  background-color: var(--light-color);
  color: var(--dark-color);
}

.container-fluid {
  width: 100%;
  min-width: 320px;
  max-width: 1440px;
  height: 104px;
  margin: 0 auto;
  padding: 0 36px;
}

.navbar {
  flex-wrap: nowrap;
  height: 120px;
}

.navbar-nav {
  gap: 32px;
}

.menu-list {
  z-index: 10000;
  visibility: hidden;
  opacity: 0;
  text-align: center;
  background-color: var(--purple-color);
  width: 100%;
  height: 0;
  min-height: 100%;
  padding-top: 20px;
  transition: transform .7s ease-in-out, opacity .3s ease-in-out;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.btn-menu-close {
  background-color: #0000;
  border: none;
  width: 48px;
  height: 48px;
  position: absolute;
  top: 40px;
  right: 80px;
}

.icon-close {
  fill: var(--dark-color);
  width: 48px;
  height: 48px;
  transition: fill .3s;

  &:hover, &:active {
    fill: var(--active-color);
  }
}

.menu-list.active {
  visibility: visible;
  opacity: 1;
  transition: transform .7s ease-in-out, opacity .3s ease-in-out;
  transform: translateX(0);
}

.list-items {
  flex-direction: column;
  gap: 54px;
  margin-top: 100px;
  display: flex;
}

.menu-item {
  color: var(--dark-color);
  font-size: 36px;
  font-weight: 500;
  line-height: 1.15;

  &:hover, &:active {
    fill: var(--active-color);
  }
}

.overlay {
  z-index: 2000;
  background: #000000b3;
  width: 100%;
  min-width: 1440px;
  height: 100vh;
  position: fixed;
  inset: 0;
}

.modal-content {
  background: var(--light-color);
  z-index: 3000;
  border-radius: 12px;
  flex-direction: column;
  width: 560px;
  height: 600px;
  padding: 40px;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hidden {
  display: none;
}

.modal-title-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.btn-primary {
  background-color: var(--purple-color);
  color: var(--dark-color);
  border: none;
  width: 160px;
  height: 40px;
  margin-top: 16px;
  font-size: 18px;
  font-weight: 500;

  &:hover, &:active {
    background-color: var(--active-color);
    color: var(--dark-color);
  }
}

@media (width <= 744px) {
  .logo-text {
    display: none;
  }

  .header {
    padding: 0 12px;
  }
}

@media (width >= 1200px) {
  .add-task-button {
    margin-left: 170px;
  }
}

@media (width >= 1400px) {
  .navbar-expand-xl .navbar-nav .nav-link {
    padding-left: 24px;
    padding-right: 24px;
  }

  .add-task-button {
    margin-left: 210px;
  }
}
